import {sum} from 'lodash';
import {StorageProps} from '../common/utils/storage';
import React, {memo} from 'react';
import {View, StyleSheet} from 'react-native';
import {Text, useTheme} from 'react-native-paper';

const JCopyright = (props: {visitInfo: StorageProps[]}) => {
  const date = new Date();
  const fy = date.getFullYear();
  const _copyright =
    fy > 2023 ? ` 2023 - ${fy} jacepark.com` : ` ${fy} jacepark.com`;
  const {colors} = useTheme();
  const visits = props.visitInfo.map(v => v.counts);
  const uniqueVisits = props.visitInfo.length;
  return (
    <View style={[styles.container, {backgroundColor: colors.background}]}>
      <Text variant="bodySmall">
        {uniqueVisits.toLocaleString()} / {sum(visits).toLocaleString()}
      </Text>
      <Text variant="bodySmall">{_copyright}</Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {padding: 20, alignItems: 'center', justifyContent: 'center'},
});
export default memo(JCopyright);
