import AsyncStorage from '@react-native-async-storage/async-storage';
import {KJA_ASYNC_KEY} from '../constants';

export interface StorageProps {
  ip: string;
  counts: number;
}

export const storeJsonData = async (value: StorageProps[]) => {
  try {
    const jsonValue = JSON.stringify(value);
    await AsyncStorage.setItem(KJA_ASYNC_KEY, jsonValue);
  } catch (e) {
    // saving error
  }
};

export const getJsonData = async (key: string) => {
  try {
    const jsonValue = await AsyncStorage.getItem(key);
    return jsonValue != null ? JSON.parse(jsonValue) : null;
  } catch (e) {
    // error reading value
  }
};

export const clearAll = async () => {
  try {
    await AsyncStorage.clear();
  } catch (e) {
    // clear error
  }

  console.log('AsyncStorage clearAll() Done.');
};
